import { formatDate } from '@/common/date.js';
import http from '@/components/utils/http.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
		components: {
			uniLoadMore	
		},
	filters: {
		formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd hh:mm');
		}
	},
	data() {
		return {
			gid: 1,
			page: 1,
			team: [],
			loadingType: 'more', //加载更多状态
		};
	},
	onLoad() {
		this.getteam();
	},
	
	onPullDownRefresh() {
	        this.getteam('refresh')
	},
	//加载更多
	onReachBottom(){
		this.page=this.page+1;
		this.getteam();
	},

	methods: {
		tabSelect(e) {
			this.gid = e.currentTarget.id;
			this.page=1;
			this.getteam('refresh');
		},

		getteam(type='add',loading) {
			uni.showLoading({
				title:"数据加载中"
			})
			var _that = this;
			let opts = {
				url: '/base/getteam/',
				method: 'post'
			};
			let param = {
				gid: _that.gid,
				page:_that.page,
			};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					uni.hideLoading();
					if (res.data['code'] == 0) {
						
						
						let teamlist = res.data['userlist'];
						if(type === 'refresh'){
							this.team = [];
						}
						_that.team = this.team.concat(teamlist);
						
						
						//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
						this.loadingType  =teamlist.length <20 ? 'nomore' : 'more';
						if(type === 'refresh'){
							if(loading == 1){
								uni.hideLoading()
							}else{
								uni.stopPullDownRefresh();
							}
						}
						
						
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		}
	}
};
